.perfect-container{
    background-color: $clr3;
    height: 745px;
    .perfect-content{
        width: 1100;
        // margin-left: 175px;
        margin: 0 175px;
        margin-top: 40px;
        h1{
            width: 932px;
            margin: 0 82px;
            font-size: 48px;
            color: $clr6;
            margin-bottom: 45px;
            text-align: center;
            span{
                color: #79c8c7;
            }
        }
        .success-stories{
            display: grid;
            grid-template-columns: 370px 370px 370px;
            text-align: center;
            color: $clr6;
            height: 313px;
            padding-top: 30px;
            .statement{
                width: 350px;
                text-align: center;
                font-size: 15px;
                font-family: livvic;
                line-height: 1.4;
            }
         
            h4{
                color: #79c8c7;
                font-size: 18px;
                margin-top: 20px;
                margin-bottom: 10px;
            }
            h5{
                margin-bottom: 30px;
                font-family: livvic-medium;
                font-size: 13px;
            }
        }
        .perfect-img{
            width: 62px;
            height: 62px;
            border-radius: 50%;
            border: 3px solid #c4FFFE ;
        }
    }

    .perfect-circle{
        position: relative;
        left: 1240px;
        bottom: -28px;
    }
}